import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

export function initHeroAnimations(scrollContainer) {
  const headerEl = document.querySelector('header');
  const heroSectionEl = document.querySelector('.hero');

  // Header Scroll Trigger
  if (headerEl && heroSectionEl && scrollContainer) {
    ScrollTrigger.create({
      trigger: heroSectionEl,
      scroller: scrollContainer,
      start: "bottom top",
      onEnter: () => headerEl.classList.add("scrolled"),
      onLeaveBack: () => headerEl.classList.remove("scrolled"),
    });
  }

  // Hero Pinning & Parallax Timeline
  if (heroSectionEl && scrollContainer) {
    gsap.timeline({
      scrollTrigger: {
        trigger: heroSectionEl,
        scroller: scrollContainer,
        pin: true,
        pinSpacing: false,
        start: "top top",
        end: "bottom top",
        scrub: 0.5,
      }
    })
    .to(heroSectionEl, { yPercent: -30, ease: "none" }, 0);
  }

  // Hero Title Staggered Animation
  const heroTitleSpans = document.querySelectorAll('.hero-title span');
  if (heroTitleSpans.length > 0) {
    gsap.from(heroTitleSpans, {
      opacity: 0,
      y: 50,
      stagger: 0.2,
      duration: 1,
      ease: 'power2.out',
      delay: 0.5 // Delay might be adjusted in main.js coordination
    });
  }
} 